

























































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator'
import MovieService from '@/services/MovieService'
import ModalTrailer from '@/components/Modals/ModalTrailer.vue'
import { getModule } from 'vuex-module-decorators'
import Movie from '@/store/modules/Movie'
import store from '@/store'
import { IMovie } from '@/models'

const MovieModule = getModule(Movie, store)

@Component({
  components: {
    ModalTrailer
  }
})
export default class MovieDetail extends Vue {
  private currentType: string = 'summary'
  private movieDetail: IMovie = new IMovie()
  private commonOptions = {
    initialSlide: 0,
    loop: true,
    slidesPerView: 1,
    spaceBetween: 10
  }

  private swiperPhotoOptions = {
    ...this.commonOptions,
    autoplay: {
      delay: 2000,
      stopOnLastSlide: false,
      disableOnInteraction: false
    },
    breakpoints: {
      768: {
        slidesPerView: 3,
        spaceBetween: 30
      },
      576: {
        slidesPerView: 2,
        spaceBetween: 20
      },
      320: {
        slidesPerView: 1,
        spaceBetween: 10
      }
    }
  }

  private swiperCastOptions = {
    ...this.commonOptions,
    navigation: {
      nextEl: '.swiper-next-cast',
      prevEl: '.swiper-prev-cast'
    },

    breakpoints: {
      1200: {
        slidesPerView: 4,
        spaceBetween: 40
      },
      992: {
        slidesPerView: 3,
        spaceBetween: 30
      },
      576: {
        slidesPerView: 2,
        spaceBetween: 20
      },
      320: {
        slidesPerView: 1,
        spaceBetween: 10
      }
    }
  }

  private swiperCrewOptions = {
    ...this.commonOptions,
    navigation: {
      nextEl: '.swiper-next-crew',
      prevEl: '.swiper-prev-crew'
    },

    breakpoints: {
      1200: {
        slidesPerView: 4,
        spaceBetween: 40
      },
      992: {
        slidesPerView: 3,
        spaceBetween: 30
      },
      576: {
        slidesPerView: 2,
        spaceBetween: 20
      },
      320: {
        slidesPerView: 1,
        spaceBetween: 10
      }
    }
  }

  created(): void {
    this.getMovieDetail()
  }

  changeType(type: string): void {
    if (type === 'summary') {
      this.currentType = 'summary'
    } else {
      this.currentType = 'userReview'
    }
  }

  getMovieDetail(): void {
    MovieService.getMovieById(this.$route.params.movieId)
      .then((response: any) => {
        if (response.status === 200) {
          this.movieDetail = response.data
          if (this.$route.params.openTrailer) {
            this.openTrailer()
          }
        }
      })
      .catch((error: any) => {
        console.log(error)
      })
  }

  openTrailer(): void {
    this.$bvModal.show('modal-trailer')
  }

  goToTicketPlan(): void {
    MovieModule.HANDLE_MOVIE_DETAIL(this.movieDetail)
    this.$router.push({ name: 'ticket-plan' })
  }
}
